.text-default {
	&::after {
		content: '';
		display: block;
		clear: both;
	}

	.pic {
		width: 50%;
		float: right;
		padding-left: 28px;
		padding-bottom: 28px;

		img {
			max-width: calc(50vw - 28px - var(--scrollbar-width) / 2);
		}
	}

	.date-published {
		color: #9a9793;
		font-size: 12px;
		margin-bottom: 22px;
	}

	.row {
		margin: 40px -10px;
		display: flex;
		justify-content: space-between;

		&:first-child {
			margin-top: 0;
		}

		img {
			width: 50%;
			padding: 0 10px;
		}
	}

	.video-block {
		margin: 40px 0;

		&:first-child {
			margin-top: 0;
		}
	}

	.delivery-page & {
		h2 {
			font-size: 26px;
			font-weight: 700;
		}

		br {
			content: '';
			display: block;
			margin: 0 0 12px;
		}
	}

	h2 {
		font-size: 22px;
		font-weight: 500;
		margin: 0 0 24px;

		&:not(:first-child) {
			margin-top: 56px;
		}
	}

	.date-published + h2 {
		margin-top: 0;
	}

	p {
		margin: 0 0 20px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	@media (--desk) {
		> *:not([class]):not(picture):not(img) {
			max-width: 89%;
		}
	}

	@media (--phone) {
		.pic {
			width: auto;
			float: none;
			padding: 0;
			margin: 0 -20px 30px;
	
			img {
				width: 100%;
				max-width: none;
			}
		}
	
		.row {
			margin: 30px -20px;
			display: flex;
			flex-wrap: wrap;
	
			img {
				width: 100%;
				padding: 0;
			}

			img + img {
				margin-top: 20px;
			}
		}
	
		.video-block {
			margin: 30px -20px;
		}
	}
}