.container {
	width: 100%;
	max-width: 1194px;
	padding: 0 15px;
	margin: 0 auto;

	& & { // если будут вложеные контейнеры в друг друга
		padding: 0;
		max-width: none;
	}

	&_smaller {
		max-width: 990px;
	}

	&_space-left {
		padding-left: 122px;
	}

	@media (--phone) {
		padding: 0 20px;
	}
}